import React, { useEffect, useState } from "react";
import { getMovieCredits } from '../../api';
import { useParams } from "react-router-dom";
import './Cast.styled';
import {StyleTCastList, StyleTCast, StyleError, StyleTitle} from './Cast.styled';



export default function Cast({title}) {
  const [movieCredits, setMovieCredits] = useState([]);
  const { movieId } = useParams();
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState(null)

  useEffect(() => {
    if (!movieId) return;
    const fetchCredits = async () => {
      try {
        if (movieId) {
          setIsLoading(true)
          const credits = await getMovieCredits(movieId);
          
          setMovieCredits(credits);
        }
      } catch (error) {
        setError(error)
         } finally {
           setIsLoading(false)
         }
       };

    fetchCredits();
  }, [movieId]);





  const baseURL = "https://image.tmdb.org/t/p/w200";

  return (
    <div>
      <StyleTitle>{title}</StyleTitle>
      {isLoading && <p>Loading...</p>}
      {error && <p>Something went wrong...</p>}
      {
        <StyleTCastList>
          {movieCredits.length > 0 ? (
            movieCredits.map(({ id, name, profile_path }) => (
              <StyleTCast key={id}>
                {name}
                <img
                  src={profile_path ? baseURL + profile_path : 'data:image/jpeg;base64,'}
                  width="200"
                  
                  alt="profile_path"
                />
              </StyleTCast>
            ))
          ) : (
            <StyleError>There is no cast list for this movie</StyleError>
          )}
        </StyleTCastList>
      }
    </div>
  );
          }  